<template>
  <div class="scrollBox">
    <div class="serarch-bar" :class="{isFocus: isFocus || content}">
      <div class="backButton" @click="routerBack"></div>
      <div class="search-content">
        <div class="search-icon" @click="search">
          <img src="../../../assets/img/movie/white_search.png" alt="搜索">
        </div>
        <input class="searchInput" ref="searchInput" :class="{isIos: $utils.isIos()}" maxlength="30" @keyup.enter="search" @input="getSearch" @focus="focusFn" @blur="blurFn" v-model="content" type="text" :placeholder="placeholderText">
        <div class="clearInput" v-show='content.length > 0' @click="clearInput"></div>
      </div>
      <div class="searchButton" @click="search">搜索</div>
    </div>
    <div class="fixedTop" v-if="list.length > 0">
      <transition name="topTxt">
        <div class="topTitle" v-if="showDrop">为保证搜索全面，未对同名资源去重，请挑选观看</div>
      </transition>
      <div class="search-tab-box" ref="searchTabBox">
        <div class="search-tab" @click="setTabSearch(index)" :ref="`activeIndex${activeIndex}`" v-for="(item, index) in searchDetail" v-if="item.count !== 0" :class="{active: index === activeIndex}" :key="index">{{item.name}} <span v-if="item.tab_name!=='全部'">{{item.count}}</span></div>
      </div>
    </div>
    <div class="search-tip" v-if="search_tip.length > 0 && isFocus && list.length === 0 && !isComplete && !isLoading && isFocus">
      <div class="item" v-for="(item, index) in search_tip" @click="setSearch(index)" :key="index">
        <div v-html="item.vod_name"></div>
      </div>
    </div>
    <div id="search" ref='search' :class="{hasTabBars: list.length > 0}">
      <div class="loadData" infinite-scroll-distance="50" v-infinite-scroll="getMore">
        <div class="searchResult" v-if="list.length > 0">
          <List class="result_list" :list='list' :type="4"></List>
          <div class="loadingComplete" v-if="isComplete">
            <div class="no_video" @click="toOther">没有找到想看的影视？</div>
            <div class="loadMore flex-row" @click="toUserQuesTion">
              <div class="text">{{isComplete ? '留言求片' : '点击加载更多资源'}}</div>
              <div class="arrowEl"></div>
            </div>
          </div>
        </div>
      </div>
      <!-- 没有搜索结果 -->
      <div class="noResult" v-if="showNoResult && isComplete && list.length === 0">
        <div class="iconSize">
          <img src="../../../assets/img/movie/search-video.png">
        </div>
        <div class="color666 lh46 pb20 text-center">搜索不到相关影片<br>换个关键词试试吧~</div>
      </div>
      <ListLoading v-if="list.length === 0 && isLoading"></ListLoading>
      <loading v-if="isLoading && page !== 1" :page="page" :loading="loading"></loading>
      <SearchHistory @searchList='searchHot' v-if="movie.searchHistory.history.length > 0 && list.length === 0 && !content"></SearchHistory>
      <hot-list @searchHot='searchHot' v-if="list.length === 0 && !content"></hot-list>
    </div>
  </div>
  
</template>
<script>
import HotList from '@/components/movie/search/hot-search'
import SearchHistory from '@/components/movie/search/search-history'
import List from '@/components/movie/common/list'
import ListLoading from '@/components/movie/common/video-loading'
import loading from '@/components/movie/common/list-loading'
import {mapState, mapMutations} from 'vuex'
export default {
  components: {
    HotList,
    SearchHistory,
    List,
    ListLoading,
    loading
  },
  computed: {
    ...mapState({
      movie: state => state.movie,
      commonState: state => state.common
    })
  },
  data () {
    return {
      showDrop: false,
      loading: 0,
      isFirst: false,
      placeholderText: '搜索-史上最大最全影视片库',
      content: '',
      showSearch: true,
      searchTemp: [],           // 由于换成了html，所以需要保存一份关键词
      showNoResult: false,
      search_tip: [],
      list: [],
      isFocus: false,
      isLoading: false,
      isComplete: false,
      total: '',
      activeIndex: 0,
      page: 1,
      tempKw: '',
      firstKw: '',
      tab_name: '',
      searchDetail: [{
        tab_name: '全部'
      }, {
        tab_name: '电影'
      }, {
        tab_name: '电视剧'
      }, {
        tab_name: '综艺'
      }, {
        tab_name: '动漫'
      }]
    }
  },
  beforeRouteLeave (to, from, next) {
    this.loading = 1
    if (this.$refs.search) {
      this.$utils.cache('searchScroll', this.$refs.search.scrollTop)
    }
    next()
  },
  methods: {
    ...mapMutations([
      'movie_setSearchHistory',
      'common_login_box'
    ]),
    focusFn () {
      this.isFocus = true
    },
    blurFn () {
      this.isFocus = false
    },
    clearInput () {
      this.content = ''
      this.list = []
      this.search_tip = []
      this.isLoading = false
      this.isComplete = false
      this.loading = 0
    },
    // 点击关键字搜索
    setSearch (index) {
      this.content = this.search_tip[index].vod_name.replace(/<span class="magic">|<\/span>/g, '')
      this.search()
      this.search_tip = []
    },
    getSearch () {
      if (this.timer || this.searchLoading > 0) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.searchLoading = 1
      setTimeout(() => {
        this.searchLoading = 0
      }, 500)
      this.timer = setTimeout(() => {
        let data = {
          kw: this.content
        }
        this.$api.get('MOVIE_SEARCH_TIP', data, res => {
          this.searchLoading = 0
          this.searchTemp = Object.assign(res.data)
          for (let v of res.data) {
            // v.vod_name = this.$utils.splitStr(v.vod_name, 25).repeat(5)
            v.vod_name = v.vod_name.replace(this.content, `<span class="magic">${this.content}</span>`)
          }
          if (this.content) {
            this.search_tip = res.data
          } else {
            this.search_tip = []
          }
          clearTimeout(this.timer)
          this.timer = null
        }, res => {
          clearTimeout(this.timer)
          this.timer = null
        })
      }, 1000)
      if (this.content === '') {
        this.search_tip = []
        this.list = []
      }
    },
    routerBack () {
      this.$router.go(-1)
    },
    searchHot (item) {
      this.content = item.chinese_name
      this.search()
    },
    setTabSearch (index) {
      this.page = 1
      this.activeIndex = index
      this.isComplete = false
      this.isLoading = false
      this.loading = 0
      this.tab_name = this.searchDetail[index].name
      this.search('change')
    },
    // 搜索结果
    search (e) {
      this.isComplete = false
      if (!this.content) {
        return
      }
      this.page = 1
      if (e !== 'change') {
        this.type = ''
        this.tab_name = ''
      }
      this.list = []
      this.getSearchResult(res => {
        if (this.content) {
          this.list = res.data.data
        } else {
          this.list = []
        }
        this.showDrop = true
        setTimeout(() => {
          this.showDrop = false
        }, 2000)
      })
      this.movie_setSearchHistory({name: this.content.trim()})
    },
    // 点击加载更多资源
    getMore () {
      if (!this.content) {
        return
      }
      this.getSearchResult(res => {
        this.list = [...this.list, ...res.data.data]
      })
    },
    toUserQuesTion () {
      if (this.isComplete) {
        this.$router.push('/AwardFeedback')
      }
    },
    getSearchResult (cb) {
      if (this.isLoading || this.isComplete) {
        return
      }
      this.isLoading = true
      this.loading = 1
      this.tempKw = this.content
      let data = {
        page: this.page,
        kw: this.content
      }
      if (this.tab_name && this.tab_name !== '全部') {
        data.type = this.tab_name
      }
      setTimeout(() => {
        this.isLoading = false
        this.loading = 0
      }, 15000)
      this.$api.get('MOVIE_SEARCH', data, res => {
        this.isLoading = true
        this.loading = 1
        if (this.page >= res.data.last_page) {
          this.isComplete = true
        }
        if (res.data.total === 0) {
          this.showNoResult = true
        }
        this.page++
        res.data.count.unshift({
          name: '全部'
        })
        this.searchDetail = res.data.count
        
        for (let v of res.data.data) {
          v.vod_name = v.vod_name.replace(data.kw, '<span class="magic">' + data.kw + '</span>')
          if (v.vod_remarks.indexOf('HD') > -1) {
            v.colorType = 1
          } else if (v.vod_remarks.indexOf('超清') > -1) {
            v.colorType = 2
          } else if (v.vod_remarks.indexOf('高清') > -1) {
            v.colorType = 3
          } else if (v.vod_remarks.indexOf('DVD') > -1) {
            v.colorType = 4
          } else if (v.vod_remarks.indexOf('正片') > -1) {
            v.colorType = 5
          } else if (v.vod_remarks.indexOf('BD') > -1) {
            v.colorType = 6
          }
        }
        this.isLoading = false
        typeof cb === 'function' && cb(res)
        this.$nextTick(() => {
          if (this.activeIndex > 2) {
            if (this.$refs['activeIndex' + this.activeIndex][this.activeIndex]) {
              this.$refs.searchTabBox.scrollLeft = this.$utils.getElLeftPotion(this.$refs['activeIndex' + this.activeIndex][this.activeIndex]) + 200
            }
          }
        })
      })
    },
    // 没有想要的电影
    toOther () {
      
    }
  },
  mounted () {
    this.$refs.searchInput.focus()
  },
  watch: {
    '$route' (to, from) {
      if (from.name !== 'FilmDetail' && from.name !== 'Search' && from.name !== 'AwardFeedback') {
        this.$refs.searchInput.focus()
        this.content = ''
        this.showNoResult = false
        this.list = []
      } else {
        if (to.name === 'Search') {
          if (this.$utils.cache('searchScroll')) {
            this.$nextTick(() => {
              this.$refs.search.scrollTop = this.$utils.cache('searchScroll')
            })
          }
        }
      }
    },
    content (content) {
      this.tab_name = ''
      this.isComplete = false
      this.list = []
      this.search_tip = []
      if (!content) {
        this.search_tip = []
        this.showNoResult = false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.scrollBox {
  .serarch-bar {
    height: 88px;
    position: relative;
    display: flex;
    background: #ffaa00;
    align-items: center;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    z-index: 1;
    &.isFocus {
      .search-content {
        .search-icon {
          margin-left: 0px;
        }
      }
    }
    .backButton {
      left: 0;
      width: 74px;
      height: 100%;
      top: 0;
      background: url('../../../assets/img/white_back.png') no-repeat center;
      background-size: 20px 35px;
    }
    .search-content {
      position: relative;
      flex: 1;
      background: #e49c0b;
      border-radius: 30px;
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding: 0 34px;
      .search-icon {
        width: 35px;
        height: 35px;
        margin-left: 100px;
        transition: margin 400ms;
        margin-top: 4px;
        img {
          width: 100%;
        }
      }
      .searchInput {
        width: 100%;
        box-sizing: border-box;
        padding-right: 40px;
        color: #fff;
        &.isIos {
          margin-top: -2px;
        }
      }
      .clearInput {
        position: absolute;
        background: url('../../../assets/img/movie/white_clear.png') no-repeat center;
        background-size: 28px 28px;
        position: absolute;
        right: 10px;
        width: 60px;
        height: 60px;
      }
    }
    .searchButton {
      width: 100px;
      text-align: center;
      font-size: 28px;
      color: #fff;
    }
  }
  .fixedTop {
    top: 88px;
    position: fixed;
    left: 0;
    right: 0;
    z-index: 2;
    background: #fff;
    .topTitle {
      height: 65px;
      overflow: hidden;
      background: #FCEAC5;
      font-size: 32px;
      text-align: center;
      line-height: 65px;
      font-size: 26px;
      color: #FFAA00;
    }
  }
  .search-tip {
    .item {
      background: #FFFFFF;
      box-shadow: 0 1px 0 0 #F5F5F5;
      height: 90px;
      display: flex;
      align-items: center;
      padding: 0 30px;
      font-size: 30px;
      > div {
        min-width: 0;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        line-height: 1.2
      }
    }
  }
  .search-tab-box {
    white-space: nowrap;
    margin: 30px 30px 15px;
    padding: 10px 0;
    overflow: auto;
    .search-tab {
      min-width: 147px;
      display: inline-block;
      padding: 0 20px;
      margin-right: 33px;
      box-sizing: border-box;
      height: 54px;
      line-height: 54px;
      font-size: 30px;
      border-radius: 54px;
      text-align: center;
      border: 1px solid #f4a828;
      color: #666;
      &.active {
        background: #f4a828;
        color: #fff;
        border: 1px solid #f4a828;
      }
    }
  }
}
#search {
  height: 100vh;
  background: #fff;
  box-sizing: border-box;
  padding-bottom: 90px;
  padding-top: 88px;
  overflow: auto;
  &.hasTabBars {
    padding-top: 188px;
  }
  .result_list {
    border-top: 1px solid #f2f2f2;
  }
  .noResult {
    padding: 146px 0;
    .iconSize {
      width: 127px;
      margin: 0 auto 30px;
    }
    .text-center {
      line-height: 50px;
    }
  }
  &.Router {
    transition: all 0s ease;
  }
  
  .searchResult {
    transition: all .5s;
    transform: translate(0, 0, 0);
    overflow: hidden;
    
    .result-tip {
      padding: 30px 30px 0;
      .small-tip {
        padding-top: 10px;
        line-height: 34px;
        color: #999;
      }
    }
   
    // 列表下按钮
    .no_video {
      text-align: center;
      padding: 40px 30px 12px;
      font-size: 24px;
      color: #999999;
    }
    .loadMore {
      flex-flow: row nowrap;
      line-height: 34px;
      color: #4A90E2;
      padding: 20px 0;
      font-size: 24px;
      .arrowEl {
        width: 14px;
        height: 14px;
        border-color: #4A90E2;
        margin-left: 6px;
      }
    }
  }
}
</style>



