<template>
  <div id="searchHistory">
    <div class="title flex-row jusB">
      <div class="leftText">搜索历史</div>
      <div class="rightIcon" @click="isClear = true">
        <img src="../../../assets/img/movie/delete.png" alt="删除">
      </div>
    </div>
    <div class="flex-history">
      <div class="history-item" @click="searchList(index)" v-for="(item, index) in movie.searchHistory.history" :key="index">{{item}}</div>
    </div>
    <prompt-box
      :dropBol="isClear"
      @leftBtn="isClear=false"
      @rightBtn="clearHistory"
      rightTxt="清空"
      contentTxt="确认清空输入记录？"
    >
    </prompt-box>
  </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import PromptBox from '../../common/prompt-box'

export default {
  components: {
    PromptBox
  },
  data () {
    return {
      isClear: false
    }
  },
  computed: {
    ...mapState({
      movie: state => state.movie
    })
  },
  methods: {
    ...mapMutations([
      'movie_setSearchHistory'
    ]),
    clearHistory () {
      this.movie_setSearchHistory({type: 'clear'})
      this.$toast({
        message: '删除成功',
        duration: 1500
      })
    },
    searchList (index) {
      this.$emit('searchList', {
        chinese_name: this.movie.searchHistory.history[index]
      })
    }
  }
}
</script>

<style lang="scss" scoped>
#searchHistory {
  margin-top: 30px;
  padding-bottom: 10px;
  border-bottom: 1px solid#EBEAEF;
  .title {
    font-size: 36px;
    color: #FF7F18;
    padding: 30px 45px 23px;
    box-sizing: border-box;
    .rightIcon {
      width: 32px;
      height: 34px;
      img {
        width: 100%
      }
    }
  }
  .flex-history {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: flex-start;
    white-space: nowrap;
  }
  .flex-history {
    margin: 0 45px;
    max-height: 252px;
    overflow: hidden;
    .history-item {
      background: #EBEAEF;
      border-radius: 3px;
      font-size: 30px;
      line-height: 37px;
      padding: 6px 14px 2px;
      max-width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      color: #666;
      margin-right: 10px;
      margin-bottom: 20px;
    }
  }
}
</style>



