<template>
  <div id='hotSearch'>
    <div v-show="list.length > 0">
      <div class="title">热搜榜</div>
      <div class="item flex-row hoz-flex-start" v-for="(item, index) in list" @click="searchHot(index)" :key="index">
        <div class="leftIcon" :class="`icon${index + 1}`">{{ index + 1 }}</div>
        <div class="name">{{item.chinese_name}}</div>
        <div class="hot flex-row" v-if="index === 0">
          <div class="hotCount" v-for="(item, index) in 3" :key="index"></div>
        </div>
        <div class="hot flex-row" v-else-if="index === 1">
          <div class="hotCount" v-for="(item, index) in 2" :key="index"></div>
        </div>
        <div class="hot flex-row" v-else-if="index === 2">
          <div class="hotCount" v-for="(item, index) in 1" :key="index"></div>
        </div>
        <div class="grade flex-row hoz-flex-end" :class="{active: index < 3}">{{item.score}}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      list: []
    }
  },
  methods: {
    getHotSearch () {
      this.$api.get('MOVIE_HOT', {}, res => {
        this.list = res.data
      })
    },
    searchHot (index) {
      this.$emit('searchHot', this.list[index])
    }
  },
  mounted () {
    this.getHotSearch()
  }
}
</script>

<style lang="scss" scoped>
#hotSearch {
  background: #fff;
  .title {
    font-size: 36px;
    color: #FF7F18;
    padding: 30px 45px 23px;
  }
  .item {
    height: 113px;
    box-sizing: border-box;
    padding: 0 40px 0 45px;
    .leftIcon {
      width: 45px;
      height: 45px;
      display: flex;
      align-content: center;
      align-items: center;
      justify-content: center;
      font-size: 32px;
      background: #D8D8D8;
      color: #666;
    }
    .icon1 {
      background: #E72600;
      color: #fff;
    }
    .icon2 {
      background: #F78105;
      color: #fff;
    }
    .icon3 {
      background: #FFBE02;
      color: #fff;
    }
    .name {
      color: #333;
      font-size: 32px;
      padding: 0 20px;
    }
    .hot {
      .hotCount {
        width: 16px;
        height: 20px;
        background: url('../../../assets/img/movie/hot.png') no-repeat center;
        background-size: 100%;
        margin-right: 4px;
      }
    }
    .grade {
      color: #999;
      font-size: 30px;
      font-weight: 900;
      flex: 1;
      &.active {
        color: #FF7F18;
      }
    }
  }
}
</style>



